import firebase from "firebase/app";
import "firebase/auth"
import Cookies from "js-cookie";
import { getUserFromCookie, getUserFromSession } from "@/helpers";
export default {
  asyncData({ req, redirect }) {
    if (process.server) {
      //console.log("server", req.header);
      const user = getUserFromCookie(req);
      //   console.log('b', getUserFromCookie(req))
      if (!user) {
        console.log("redirecting server");
        redirect("/login");
      }
    } else {
      var user = firebase.auth().currentUser;
      if (!user) {
        redirect("/login");
      }
      //   console.log($nuxt.$router)
    }
  },
  data: () => ({
    valid: false,
    checkbox: true,
    loggedIn: true,
    clientName: "",
    clientPlan: "",
    currentInvestDate: "",
    dueDate: "",
    email: "",
    initialDate: "",
    investmentState: "",
    nextOfKinEmail: "",
    nextOfKinName: "",
    nextOfKinPhoneNum: "",
    phoneNumber: "",
    investmentStateType: ["ongoing", "dormant", "pending"],
    nameRules: [
      (v) => !!v || "Name is required",
      (v) => (v && v.length >= 5) || "Name greater than 5 characters",
    ],
    clients: [],
    emailRules: [
      (v) => !!v || "E-mail is required",
      (v) => /.+@.+/.test(v) || "E-mail must be valid",
    ],
  }),

  methods: {
    getClients() {
      this.$fireStore.collection("clients").onSnapshot((data) => {
        data.forEach((e) => {
          this.clients.push({ ...e.data(), id: e.id });
        });
      });
    },
    addClient() {
      let client = {
        clientName: this.clientName,
        clientPlan: this.clientPlan,
        currentInvestDate: this.currentInvestDate,
        dueDate: this.dueDate,
        email: this.email,
        initialDate: this.initialDate,
        investmentState: this.investmentState,
        nextOfKinEmail: this.nextOfKinEmail,
        nextOfKinName: this.nextOfKinName,
        nextOfKinPhoneNum: this.nextOfKinPhoneNum,
        phoneNumber: this.phoneNumber,
      };
      console.log(client);

      this.$fireStore
        .collection("clients")
        .add(client)
        .then((e) => alert(" document sent" + e.id))
        .catch((e) => console.log(e));
    },

    validateClient() {
      this.$refs.form.validate();
      this.addClient();
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },

    deleteClient(doc) {
      this.$fireStore
        .collection("clients")
        .doc(doc.id)
        .delete()
        .then(() => {
          console.log("deleted succesfully");
        })
        .catch((e) => console.log(e));
    },
    signOut() {
      this.$fireAuth
        .signOut()
        .then((e) => {
          this.$router.push("/login");
          // Sign-out successful.
          console.log(e);
        })
        .catch(function (error) {
          // An error happened.
          console.log(error);
        });
      this.$fireAuth.onAuthStateChanged((user) => {
        if (user) {
          // User is signed in.
          console.log("signed in");
          this.$fireAuth.currentUser
            .getIdToken(true)
            .then((token) => Cookies.set("access_token", token));
          this.loggedIn = true;
        } else {
          Cookies.remove("access_token");
          // if (Cookies.set('access_token', 'blah')) {
          // }
          // No user is signed in.
          this.loggedIn = false;
          console.log("signed out", this.loggedIn);
        }
      });
    },
  },
  async beforeMount() {
    this.getClients();
  },
};
